                          <!-- <object v-if="!item.etag" style="width:100%;height:100%;float: none; clear: both; margin: 2px auto;" :data="'https://www.youtube.com/embed/'+ item.id.videoId +'?autoplay=1&amp;rel=0&amp;showinfo=0'"> -->
